}
#scrollToTop {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  width: 40px;
  height: 40px;
  border-radius: 30px;
  padding: 3px;
}
#scrollToTop i {
  left: 4px;
  align-content: center;
}
#show-all {
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: #336699;
  z-index: 99;
}
@media screen and (max-width: 767px) {
  .mobile-hidden {
    visibility: hidden;
    clear: both;
    display: none;
