.editable {
  border: 1px dashed #d3d3d3;
}
.input-transparent {
  background: transparent;
  border: 1px dashed #ECECEC;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.paystub-header {
  border-collapse: separate;
  border-radius: 5px;
  background-color: #006DCC;
  background-image: linear-gradient(to bottom, #00abff, #006DCC);
  color: white;
}
.paystub-header h1 {
  padding-left: 5px;
  padding-top: 15px;
}
.paystub-header small {
  color: #EEEEEE;
}
.paystub-header .row {
  padding: 5px;
}
.paystub-break {
  clear: both;
  width: 100%;
  background-color: #939393 !important;
  height: 4px;
  border-radius: 3px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.datepicker-hot {
  position: relative;
  z-index: 900;
}
.nav .navbar-nav .navbar-right {
  z-index: 901;
}
.paystub table {
  border-collapse: separate;
  border-radius: 0 5px;
}
.paystub table thead th {
  background-color: #006DCC;
  color: white;
}
.paystub table tbody td {
  background-color: #F0F8FF;
}
.paystub table tr td:first-child,
.paystub table tr th:first-child {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.paystub table tr td:last-child,
.paystub table tr th:last-child {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
