  max-width: 900px;
  max-height: 500px;
}
.wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%;
}
.icon-link {
  color: inherit;
  text-decoration: none;
  border: 0;
  padding: 2px;
}
.icon-link:hover {
  color: #919191;
  cursor: pointer;
}
.control-label {
  font-size: 16px;
}
.jsdata {
  display: none !important;
}
.jumbotron {
  margin: 5px 0 5px 0;
  background-color: #b3cce6 !important;
  border: 1px solid #336699;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.box {
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid transparent;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.box-content {
  padding: 15px;
}
.box-default {
  border-color: #ddd;
}
.box-primary {
  background-color: #79a6d2;
  color: #ECECEC;
}
.box-primary > .blockquote > .footer {
  color: #ECECEC;
}
.box-title {
  padding: 10px 15px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  color: inherit;
}
.box > .box-footer {
  padding: 10px 15px;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
}
.img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}
.yt-vid-container {
  position: relative;
  padding-bottom: 35%;
  padding-top: 0;
  height: 0;
  overflow: hidden;
}
.yt-vid-responsive {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 0;
  left: 0;
  max-width: 853px;
  width: 95%;
  height: 100%;
}
.label {
  display: inline;
  padding: .2em .6em .3em;
  font-weight: bold;
  line-height: 1;
  color: #000;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;
}
#carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 250px;
}
#carousel div * {
  display: flex;
  align-self: center;
  justify-self: center;
}
#carousel div img {
  display: block;
  margin: 0 auto;
  max-height: 240px;
}
.carousel-arrows {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
[class^="fa-circle-o-notch"],
[class*=" fa-circle-o-notch"] {
  display: inline-block;
}
td i .fa-circle-o-notch {
  text-align: center;
}
.social-icons span a i {
  padding: 0 5px 0 5px;
}
.navbar-default .navbar-nav li a {
  color: #FDFDFD !important;
}
.navbar-default .navbar-nav li :hover {
  background-color: #ffffff;
}
.navbar-default .navbar-nav li.active a {
  color: #336699 !important;
}
